<template>
    <div class='content'>
        <div class='content__lineitem'>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Model:</div>
            <div>{{baseline}}</div>
                <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Location:</div>
            <div>Mars</div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Duration:</div>
            <div>
                {{duration}}
                {{durationUnits}}
            </div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Inhabitants:</div>
            <div>
                {{humans}}
                humans
            </div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Crew Quarters:</div>
            <div>
                {{crewQuarters}}
            </div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Food Mass:</div>
            <div>
                {{foodMass}}
                kg
            </div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Life Support:</div>
            <div>
                {{lifesupport}}
                racks
            </div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Greenhouse:</div>
            <div>
                {{greenhouse}}
            </div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Plants:</div>
            <div>{{seeds}} species</div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Power Generation:</div>
            <div>
                20
                panels </div>
            <fa-icon class='fa-icon' :icon="['fal','info-circle']" style='margin:auto auto; color:grey;'/>
            <div>Power Storage:</div>
            <div>{{powerStorage}}
 
            </div>
            
        </div>  
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
export default {
    computed:{
        ...mapState({
            baselineModel: state => state.sessionConfiguration.baselineModel.model,
            location: state => state.sessionConfiguration.location.location,
            duration: state => state.sessionConfiguration.duration.amount,
            durationUnits: state => state.sessionConfiguration.durationUnits.type,
            humans: state => state.sessionConfiguration.humans.amount,
            foodMass: state => state.sessionConfiguration.foodMass.amount,
            seeds: state => state.sessionConfiguration.seed.length,
            lifesupport: state => state.sessionConfiguration.lifesupport.amount,
            crewQuartersSize: state => state.sessionConfiguration.selectedCrewQuarters,
            greenhouseSize: state => state.sessionConfiguration.selectedGreenhouse,
            powerGeneration: state => state.sessionConfiguration.powerGeneration,
            powerStorage: state => state.sessionConfiguration.powerStorage.amount,
            powerRacks: state => state.sessionConfiguration.powerRacks.amount,

            baseline: function(){
                var value = this.baselineModel;
                var models = {'baselineModel':'Baseline Model','baselineModelOne':'Baseline Model One','baselineModelTwo':'Baseline Model Two','custom':'Custom Model'}
                return models[value];
            },

            crewQuarters: function(){
                var quarters = {'none':'none','small':"Crew Quarters Small",'medium':'Crew Quarters Medium','large':'Crew Quarters Large'}
                return quarters[this.crewQuartersSize];
            },

            greenhouse: function(){
                var quarters = {'none':'none','small':"Greenhouse Small",'medium':'Greenhouse Medium','large':'Greenhouse Large'}
                return quarters[this.crewQuartersSize];
            },

            powerSupply: function(){
                var power = {
                    'solar':'Solar Array',
                    'isotope':'Radio Isotope Generator'}
                return power[this.powerGeneration];
            },            
        })
    },
}
</script>

<style lang='scss' scoped>
    @import '../scss/_theme';
    .content{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .content__lineitem{
        @extend %font-line-title;
        width: 100%;
        display: grid;
        grid-template-columns: auto auto 1fr;
        grid-template-rows: repeat(auto-fit,minmax(0px, auto));
        grid-column-gap: 8px;
        grid-row-gap: 4px;
    }
</style>
