<template>
    <div class='content'>
        <div style="width:100%; height: 100%; display:grid; grid-row-gap: 16px;grid-template-rows: auto auto;">   
            <div style='width:100%; height: 100%;'><chart chartID ="co2" :chartLabels="['CO2']" :chartColors="this.co2Color" :chartValues="[this.co2Ratio.amount.toFixed(5),this.co2Remainder]" :chartType="'doughnut'"/></div>
            <div style='width:100%; height:100%;  color:white; text-align:center;'>Carbon Dioxide</div>
        </div>
        <div style="width:100%; height: 100%; display:grid;grid-row-gap: 16px; grid-template-rows: auto auto">      
            <div style='width:100%; height: 100%;'><chart chartID ="o2" :chartLabels="['Oxygen']" :chartValues="[this.o2Ratio.amount.toFixed(5),this.o2Remainder]" :chartColors="this.co2Color" :chartType="'doughnut'"/></div>
            <div style='width:100%; height:100%; grid-row-gap: 16px; color:white;text-align:center;'>Oxygen</div>  
        
        </div>
        <div style="width:100%; height: 100%; display:grid;grid-row-gap: 16px; grid-template-rows: auto auto">      
            <div style='width:100%; height: 100%;'><chart chartID ="consumed" :chartLabels="['Produced','Consumed']" :chartValues="[this.kwhEnrgProduced.amount.value,this.kwhEnrgConsumed.amount.value]"  :chartColors="[ '#00ab98','#cb002b']" :chartType="'doughnut'"/></div>
            <div style='width:100%; height:100%; grid-row-gap: 16px; color:white;text-align:center;'>Energy Produced | Consumed</div>
        
        </div>
        <!--<div style="width:100%; height: 100%; display:grid; grid-row-gap:16px; grid-template-rows: auto auto">      
            <div style='width:100%; height:100%; color:white;text-align:center;'>Energy Produced</div>   
            <div style='width:100%; height: 100%;'><chart chartID ="produced" :chartLabels="['Free Space', 'Astronauts', 'Food', 'Structures', 'Plants']" :chartValues="[10,20]" :chartColors="[ '#00ab98','#cb002b']" :chartType="'doughnut'"/>
        </div>
        </div>-->
    </div>
</template>

<script>
import Chart from '@/components/Chart'
import {mapState,mapMutations} from 'vuex'
export default {
    components:{
        'chart':Chart,
    },
    computed:{
        ...mapState({            
            population: state => state.sessionStep.human_agent.amount,
            currentTime: state => state.sessionStep.currentTime,
            co2Ratio: state => state.sessionStep.atmo_co2_ratio,
            o2Ratio: state => state.sessionStep.atmo_o2_ratio,
            kwhEnrgProduced: state => state.sessionStep.kwh_enrg_produced,
            kwhEnrgConsumed: state => state.sessionStep.kwh_enrg_consumed,
        }),

        co2Remainder: function(){          
            return Math.max(0,(.013 - this.co2Ratio.amount).toFixed(4));
        },

        o2Remainder: function(){
            return Math.max(0,(.25 - this.co2Ratio.amount).toFixed(4));
        },


    },
}
</script>

<style lang="scss" scoped>
    .content{
        display:grid;
        grid-template-rows: repeat(3,auto);
        grid-template-columns: 1fr 1fr;
        grid-row-gap:16px;
        grid-column-gap:8px;
    }

    .gauge{
        display:grid;
        grid-template-rows: auto auto;
        grid-row-gap: 16px;
    }
</style>
